import { useState, useCallback, useMemo, useRef } from "react";
import {
  BarChart, Bar, ScatterChart, Scatter, XAxis, YAxis, CartesianGrid,
  Tooltip, ResponsiveContainer, Cell, RadarChart, Radar, PolarGrid,
  PolarAngleAxis, PolarRadiusAxis, Legend
} from "recharts";

//  Scoring Engine 
function scoreMolecule(row) {
  let score = 0;
  const flags = [];

  const get = (k) => {
    const val = row[k];
    if (val === undefined || val === null || val === "") return null;
    const n = parseFloat(val);
    return isNaN(n) ? null : n;
  };

  const pic50 = get("Predicted pIC50");
  if (pic50 !== null) {
    if (pic50 >= 6.5) score += 2;
    else if (pic50 >= 6.0) score += 1;
  }

  const dock = get("Docking score");
  if (dock !== null) {
    if (dock <= -7.0) score += 2;
    else if (dock <= -6.0) score += 1;
  }

  const logp = get("LogP");
  if (logp !== null && logp >= 0 && logp <= 5) score += 1;

  const logs = get("LogS");
  if (logs !== null && logs > -4) score += 1;

  const qed = get("QED");
  if (qed !== null && qed >= 0.5) score += 1;

  const caco = get("Caco 2R");
  if (caco !== null && caco > -5.15) score += 1;

  const hia = get("HIA");
  if (hia !== null && hia >= 80) score += 1;

  const pains = get("PAINS");
  if (pains !== null && pains === 0) score += 1;

  const toxCols = ["Hepatotoxicity", "Mutagenicity", "Carcinogenicity", "Cardiotoxicity", "Nephrotoxicity"];
  const cleanCount = toxCols.filter(c => {
    const v = get(c);
    return v !== null && v === 0;
  }).length;
  if (cleanCount === 5) score += 2;
  else if (cleanCount >= 3) score += 1;

  const flagLabels = {
    Mutagenicity: "Mutagen", Hepatotoxicity: "Hepatotox",
    Carcinogenicity: "Carcino", Cardiotoxicity: "Cardiotox", Nephrotoxicity: "Nephrotox"
  };
  Object.entries(flagLabels).forEach(([col, label]) => {
    const v = get(col);
    if (v !== null && v === 1) flags.push(label);
  });
  if (pains !== null && pains === 1) flags.push("PAINS");

  let tier;
  if (score >= 10 && flags.length === 0) tier = "Tier 1";
  else if (score >= 8) tier = "Tier 2";
  else if (score >= 6) tier = "Tier 3";
  else tier = "Tier 4";

  return { score, tier, flags };
}

//  CSV Parser 
function parseCSV(text) {
  const lines = text.trim().split(/\r?\n/);
  const headers = lines[0].split(",").map(h => h.trim().replace(/^\uFEFF/, ""));
  return lines.slice(1).filter(l => l.trim()).map(line => {
    const values = [];
    let cur = "", inQ = false;
    for (let i = 0; i < line.length; i++) {
      if (line[i] === '"') { inQ = !inQ; continue; }
      if (line[i] === "," && !inQ) { values.push(cur); cur = ""; continue; }
      cur += line[i];
    }
    values.push(cur);
    const obj = {};
    headers.forEach((h, i) => { obj[h] = values[i]?.trim() ?? ""; });
    return obj;
  });
}

